import service from "../utils/request";

export function Ping(addr){
    return service({
        url: '/ping',
        method: 'post',
        data: {
            addr: addr
        }
    })
}
export function Traceroute(addr){
    return service({
        url: '/traceroute',
        method: 'post',
        data: {
            addr: addr
        }
    })
}
export function FTP(){
    return service({
        url: '/ftpcheck',
        method: 'get'
    })
}
export function Shell(shell){
    return service({
        url: '/shellcheck',
        method: 'post',
        data: {
            shell: shell
        }
    })
}


